import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';
import { ToolMessagesDTO } from 'src/app/shared/interfaces/tools/tool-messages.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class Carrier411MessagesResolver implements Resolve<ServiceResultDTO<ToolMessagesDTO>> {

  constructor(private toolService: ToolService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<ToolMessagesDTO>> {    
    return this.toolService.getToolMessages(ToolsList.Carrier411);
  }
}
